*{
  box-sizing: border-box;
}

header{
  height: 110px;
  align-items: center;
  display: flex;
  justify-content: space-between;
  text-decoration: none;
}

header a{
  text-decoration: none;
  font-weight: 600;
  color: #4D4D4D;
  padding:  5px 20px;
}

header .logo{
  color: black;
  font-family: 1000;
  font-size: 25px;
}

header a:hover{
  color: #161616;
  text-decoration: underline;
}


/* Home page */
.home-container{
  background-image: url("./Image/mountain.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 500px;
  width: 100%;
}

.home-container h1{
  padding: 180px 0 0 0;
  display: flex;
  justify-content: center;
  color: #1e85d2f7;
}

.home-container p{
  line-height: 24px;
  display: flex;
  justify-content: center;
  color: #1e85d2;
}

.home-container a{
  display: inline-block;
  text-align: center;
  text-decoration: none;
  background-color: #ff8c38;
  border: none;
  width: 55%;
  margin-top: 27px;
  padding-block: .75rem;
  margin-left: 300px;
  color: white;
  font-weight: 700;
  cursor: pointer;
  border-radius: 5px;
}

/* about-page */

.about-content{
  padding-inline: 23px;
  color: #161616;
  margin-bottom: 55px;
}
.about-content h1{
  line-height: 38px;
}

.about-content p{
  line-height: 22px;
}

.about-cta{
  background-color: #ffcc8d;
  color: black;
  padding-inline: 32px;
  padding-bottom: 32px;
  border-radius: 5px;
  margin-inline: 35px;
}

.about-cta h2{
  margin: 0;
  padding-block: 37px;
}

.about-cta .link-button{
  background-color: black;
  border-radius: 10px;
  color: white;
  padding: 15px;
  text-decoration: none;
  cursor: pointer;
}


